function TopBar({ darkMode, setDarkMode }) {
  const menuItems = [
    { name: "Organiser", path: "/organiser" },
    { name: "Recipes", path: "/recipes" },
    { name: "About", path: "/about" },
  ];

  return (
    <header
      className={`fixed top-0 left-0 w-full flex justify-between items-center p-4 transition-colors ${
        darkMode ? "bg-gray-800" : "bg-white"
      } shadow-md z-10`}
    >
      <div className="flex items-center ml-64">
        <Image src="/mug.png" width="40" height="49" alt="Logo" />
        <h1 className="text-xl font-semibold">potteringabout</h1>
      </div>
      <nav className="space-x-4">
        {menuItems.map((item) => (
          <Link key={item.path} href={item.path}>
            {item.name}
          </Link>
        ))}
      </nav>
      <button
        onClick={() => setDarkMode(!darkMode)}
        className="p-2 rounded-full bg-gray-200 dark:bg-gray-700 transition-colors"
      >
        {darkMode ? "‚òÄÔ∏è" : "üåô"}
      </button>
      <LogoutButton />
    </header>
  );
}

export default function RootLayout({ children }) {
  return (
    <html lang="en" className="bg-[#BBB2A9]">
      <AuthProvider>
        <DarkModeProvider>
          <BodyWithAuthCheck>{children}</BodyWithAuthCheck>
        </DarkModeProvider>
      </AuthProvider>
    </html>
  );
}

function Loading() {
  return (
    <body className="flex justify-center items-center h-screen">
      Loading...
    </body>
  );
}

function Login() {
  return (
    <body className="bg-[#BBB2A9]">
      <main className="flex">
        <LoginForm />
      </main>
    </body>
  );
}

function Body({ children }) {
  const [darkMode, setDarkMode] = useState(
    localStorage.getItem("darkMode")
      ? localStorage.getItem("darkMode") === "true"
      : false
  );

  useEffect(() => {
    console.log("Main : Dark mode changed to " + darkMode);
    localStorage.setItem("darkMode", darkMode);
  }, [darkMode]);

  return (
    <body>
      <div
        className={`flex h-screen transition-colors ${
          darkMode ? "bg-gray-900 text-white" : "bg-gray-100 text-gray-900"
        }`}
      >
        <div className="flex-1 flex flex-col ml-64">
          {/* Header */}
          <TopBar darkMode={darkMode} setDarkMode={setDarkMode} />

          {darkMode, children}
          
        </div>
      </div>
    </body>
  );
}

function BodyWithAuthCheck({ children }) {
  const { authChecked, isLoggedIn } = useAuth();

  if (!authChecked) {
    return <Loading />;
  }

  return isLoggedIn ? <Body>{children}</Body> : <Login />;
}
